*{
    margin:0px;
    padding:0px;
    box-sizing: border-box;
}

/* move the game board to the center */
.container{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height:100vh;
    background: linear-gradient(skyblue 40%,green);
}

/* game board */
.game-board{
    height:80%;
    width:60%;
    box-shadow: inset -10px -10px 10px rgba(0,0,0,0.2),
                inset 10px 10px 10px rgba(0,0,0,0.2);
    border-radius: 11px;
    background-image: url(background.jpg);
    background-size:cover;
    background-position:center center;
    overflow:hidden;
    display:flex; /*so that pipe-pairs will be at equal distance*/
    justify-content: space-evenly;
    position:relative;
}


.pipe-up,.pipe-down{
    background:linear-gradient(90deg,lightgreen 1%,green);
    width:100px;
    
}


.pipe-pair{
    display:flex;
    flex-direction:column;
    height:100%;
    justify-content: space-between;
    
    position:absolute;
    top:0px;
    right:-100px;

    /* animation-name:block-movement; */
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-delay:1s;
}

.pipe-pair:nth-of-type(2)
{
    animation-delay:2s;
}
.pipe-pair:nth-of-type(3)
{
    animation-delay:3s;
}


/* initial height of the first pipe and third pipe*/
.pipe-up{
    height:38%;
    position:relative;
}
.pipe-down{
    height:calc(40% - 50px);
    position:relative;
}

.pipe-up::after{
    content:"";
    display:block;
    background-color:green;
    width:110px;
    height:25px;
    position:absolute;
    bottom:0px;
    left:-5px;
    border-radius:4px;
}
.pipe-down::after{
    content:"";
    display:block;
    background-color:green;
    width:110px;
    height:25px;
    position:absolute;
    /* bottom:0px; */
    left:-5px;
    border-radius:4px;
}

/* initial height of second pipe */
.pipe-pair:nth-of-type(2) .pipe-up
{
    height:calc(40% - 50px);
    animation-delay:2s;
}
.pipe-pair:nth-of-type(2) .pipe-down
{
    height:38%;
    animation-delay:2s;
}

/* styling bird */
.bird{
    /* outline:2px solid red; */
    position: absolute;
    left:30px;
    top:50%;
    transform: translateY(-50%);
    transition: transform 0.5s linear;
}
.bird img::selection{
    background-color:transparent;
}

.bird.jump{
    transform: rotate(-30deg);
}



.on-top{
    position:absolute;
    left:0px;
    top:0px;
    height:100vh;
    width:100vw;
    display:flex;
    justify-content: center;
    align-items: center;
    background:rgba(0,0,0,0.5);
}

.btn{
    transform: translateX(-50%),
               translateY(-50%);
    background-color:#3492db;
    color:white;
    padding:11px 34px;
    font-size:16px;
    border:none;
    border-radius: 11px;
    cursor: pointer;

    
}

.score-display{
    margin-bottom:30px;
    color:white;
    font-size: 30px;
}

@keyframes block-movement{
    0%{
        right:-100px;
    }
    100%{
        right:800px;
    }
}

